import './Header.css';
import test from './data.json'
import {Outlet,Link} from "react-router-dom";

function header() {
    return (
        <div className="Header">
            <nav className='TopLinks'>
                <Link className='subLink' to="ieee">ieee</Link>
                <Link className='subLink' to="int">int</Link>
                <Link className='subLink' to="apple2">Apple2</Link>
            </nav>
            <Outlet />
        </div>
    );
}

export default header;
